export default {
	name: 'zy-grid',
	props: {
		col: { //每行显示格子数，支持每行3个或4个(默认为3)
			type: Number,
			value: 3
		},
		showTip: { //格子菜单的数字角标(默认显示)
			type: Boolean,
			value: true
		},
		gridList: {
			type: Array,
			value: []
		}
	},
	methods: {
		gridClick(item, index) { //格子菜单点击事件
			console.log(item);
			console.log(index);
			switch (index) {
				case 0:
					uni.navigateTo({
						url: "../defect/defectList"
					});
					break;
				case 1:
					uni.navigateTo({
						url: "../health/health"
					});

					break;
				default:
					uni.showToast({
						title: "功能暂未开放",
						icon: "none"
					})
					break;
			}
		}
	}
}
